header.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header.header > div.logo-wrapper {
  display: flex;
  align-items: center;
}

header.header > div.logo-wrapper > a {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

header.header > div.logo-wrapper > a > img {
  width: 110px;
  height: 110px;
  margin-left: 10px;
}

header.header > div.logo-wrapper > a > p {
  color: black;
  font-weight: 500;
  margin-left: -20px;
}

header.header > div.header-options-container {
  display: flex;
  justify-content: center;
  align-content: center;
  grid-gap: 30px;
  gap: 30px;
  margin-left: auto;
  margin-right: 50px;
}

header.header > div.header-options-container > ul.header-options-wrapper {
  list-style: none;
}

header.header > div.header-options-container > ul.header-options-wrapper > li.header-option-list {
  font-size: 18px;
}

header.header > div.header-options-container > ul.header-options-wrapper > li.header-option-list > a {
  color: black;
}

header.header > div.header-options-container > ul.header-options-wrapper > li.header-option-list > a:hover {
  color: #808080;
  transition: all 0.1s ease-in-out;
}
