div.intro-section-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 4em;
}

div.intro-section-wrapper > div.profile-content {
  display: flex;
  flex-direction: column;
}

div.intro-section-wrapper > div.profile-content > p.greet {
  font-size: 35px;
  font-weight: 700;
  margin-left: 8px;
}

div.intro-section-wrapper > div.profile-content > h1.name {
  font-size: 150px;
  font-weight: 700;
}

div.intro-section-wrapper > div.profile-content > p.titles {
  font-size: 30px;
  margin: 8px;
  font-weight: 700;
}

div.intro-section-wrapper > div.profile-content > div.social-links-wrapper {
  display: flex;
  margin-left: 8px;
}

div.intro-section-wrapper > div.profile-content > div.social-links-wrapper > div.social-links-list {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
}

div.intro-section-wrapper > div.profile-content > div.social-links-wrapper > div.social-links-list > a {
  display: flex;
}

div.intro-section-wrapper > div.profile-content > div.social-links-wrapper > div.social-links-list > a > button {
  padding: 8px;
