  border-radius: 100%;
  cursor: pointer;
}

div.intro-section-wrapper > div.profile-content > div.social-links-wrapper > div.social-links-list > a > button:hover {
  background-color: #dadada;
}

div.intro-section-wrapper > div.git-image {
  display: flex;
}

div.intro-section-wrapper > div.git-image > img {
  width: 500px;
  cursor: pointer;
