div.contact-us {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-right: auto;
  margin-left: auto;
}

div.contact-us > h1.contact-us-title {
  margin-top: 1em;
  font-size: 80px;
  text-align: center;
  font-family: inherit;
  font-weight: 700;
  color: black;
  margin-right: auto;
  margin-left: auto;
}

div.contact-us > div.form-section-wrapper {
  display: flex;
  justify-content: space-between;
  margin-right: auto;
  margin-left: auto;
  grid-gap: 10px;
  gap: 10px;
  margin-top: 4em;
  width: 50em;
  height: 30em;
  background: #ffffff;
  border: 2px solid #dadada;
  border-radius: 20px;
  box-shadow: 0px 0px 5px #ccc;
}

div.contact-us > div.form-section-wrapper > img.contact-illustration {
  width: 300px;
}

div.contact-us > div.form-section-wrapper > form.contact-us-form {
  display: flex;
  flex-direction: column;
  margin-top: 3em;
  margin-right: 5em;
}

div.contact-us > div.form-section-wrapper > form.contact-us-form > label {
  color: black;
  font-family: inherit;
  font-size: 18px;
  font-weight: 600;
  margin-top: 1em;
}

div.contact-us > div.form-section-wrapper > form.contact-us-form > label > input {
  display: block;
  padding: 0.8em 1em;
  border-radius: 10px;
  margin-top: 0.5em;
  border: 2px solid black;
}

div.contact-us > div.form-section-wrapper > form.contact-us-form > label > input:focus {
  outline: none;
}

div.contact-us > div.form-section-wrapper > form.contact-us-form > textarea {
  margin-top: 0.5em;
  border-radius: 10px;
  padding: 0.6em 0.8em;
  border: 2px solid black;
}

div.contact-us > div.form-section-wrapper > form.contact-us-form > textarea:focus {
  outline: none;
}

div.contact-us > div.form-section-wrapper > form.contact-us-form > a.submit-form-button {
  margin-top: 0.6em;
}
